/*--------------------------------------------------------------
    1.Default CSS
----------------------------------------------------------------*/
body {
  font-size: 15px;
  line-height: 1.65;
  font-weight: 400;
  color: #666666;
  background-attachment: fixed;
  background-repeat: repeat;
}

.box-layout {
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
  background-color: #fff;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  border: 0px solid;
  outline: none;
}

a:hover,
a:active,
a:focus {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  color: #222222;
  margin: 0 0 15px;
}

input[type=text],
input[type=email],
input[type=password],
input[type=tel],
input[type=url],
input[type=search],
textarea {
  border: 1px solid #ddd;
  padding: 10px;
}

input[type=submit],
button[type=submit] {
  border: none;
  background-color: #065FD4;
  color: #fff;
  padding: 11px 20px;
  -webkit-transition: 0.25s ease-in;
  transition: 0.25s ease-in;
}

input[type=submit]:hover,
button[type=submit]:hover {
  background-color: #333333;
}

.wpcf7-form label {
  width: 100%;
}
.wpcf7-form input, .wpcf7-form textarea {
  width: 100%;
}
.wpcf7-form textarea {
  height: 110px;
}

.button {
  background: #065FD4 !important;
  color: #fff !important;
  padding: 8px 20px;
}

blockquote {
  border-left: 5px solid;
}

::-webkit-input-placeholder {
  color: gray;
  -webkit-transition: opacity 250ms ease-in-out;
  transition: opacity 250ms ease-in-out;
}

.section-padding {
  padding: 70px 0;
  background: #fafafa;
  overflow: hidden;
}

/*-------[ Default Responsive ] ------*/
@media (max-width: 767px) {
  body {
    font-size: 14px;
  }

  blockquote {
    font-size: 16px;
    font-style: italic;
  }

  .error-title h1 {
    font-size: 80px;
  }

  .error404 h3 {
    line-height: 1.3;
  }
}
img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em;
  margin-bottom: 20px;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em;
  margin-bottom: 20px;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%;
}

.wp-caption img[class*=wp-image-] {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption .wp-caption-text {
  margin: 0.8075em 0;
}

.wp-caption-text {
  text-align: center;
  font-size: 14px;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
  margin-bottom: 20px;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-3 .gallery-item {
  max-width: 33.33333%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.66667%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.28571%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.11111%;
}

.gallery-caption {
  display: block;
  margin-top: 5px;
  font-size: 14px;
}

