/*--------------------------------------------------------------
    5.Blog CSS
----------------------------------------------------------------*/
.search article.post .entry-summary {
  display: none;
}

.search article.page {
  margin-bottom: 50px;
}

article.post {
  margin-bottom: 50px;
  border: 1px solid #eee;
  box-shadow: 1px 1px 17px 1px #eee;
}

.entry-content ul {
  list-style: disc inside;
}
.entry-content ol {
  list-style: decimal;
}
.entry-content h6,
.entry-content h5,
.entry-content h4,
.entry-content h3,
.entry-content h2,
.entry-content h1 {
  margin-top: 0px;
  margin-bottom: 20px;
}
.entry-content address {
  border: 1px solid #ddd;
  padding: 10px;
}

.dostart-post {
  background: #fff;
  border-radius: 6px;
  overflow: hidden;
}
.dostart-post .post_thumbnail {
  position: relative;
}
.dostart-post .excerpt-date {
  position: absolute;
  bottom: 0;
  left: 0;
  color: #fff;
  padding: 10px 20px;
  border-top-right-radius: 30px;
  background: #065fd4;
}
.dostart-post-content {
  padding: 30px;
}
.dostart-post-content .tags > a {
  transition: 0.4s;
  border: 2px solid;
  padding: 3px 15px;
  border-radius: 20px;
  margin: 2px;
  display: inline-block;
}
.dostart-post-content .tags > a:hover {
  background: #065fd4;
  color: #fff;
}
.dostart-post .entry-meta {
  font-size: 14px;
  padding-bottom: 20px;
}
.dostart-post .entry-meta a {
  color: #333;
}
.dostart-post .entry-meta a:hover {
  color: #065fd4;
}
.dostart-post .entry-title {
  font-weight: 700;
}
.dostart-post .entry-title a {
  color: #333;
}

.excerpt-readmore a {
  display: inline-block;
  padding: 10px 30px;
  color: #fff;
  border-radius: 50px;
  background: #065fd4;
}
.excerpt-readmore a:hover {
  background: #222;
}

blockquote {
  font-size: 16px;
  font-style: italic;
  padding-left: 10px;
  border-color: #065FD4;
}

.blog-sidebar .widget {
  border: 1px solid #efefef;
  padding: 20px 15px;
}

/*-------[ Pagination ] ------*/
.pagination {
  display: unset;
  margin-top: 50px;
}
.pagination .nav-links {
  margin: 0 auto;
}
.pagination .nav-links .page-numbers {
  display: inline-block;
  padding: 9px 18px;
  border-radius: 6px;
  background: #eaeaea;
  color: #333;
}
.pagination .nav-links .page-numbers:hover {
  color: #fff;
  background-color: #065fd4;
}
.pagination .nav-links .page-numbers.current {
  color: #fff;
  background-color: #065fd4;
}

/**--------- Blog Previous Next Button ----------------*/
.navigation.post-navigation .nav-links {
  border-bottom: 1px solid #ddd;
  border-top: 1px solid #ddd;
  margin-bottom: 30px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
.navigation.post-navigation .nav-links .nav-next a,
.navigation.post-navigation .nav-links .nav-previous a {
  display: inline-block;
  border-radius: 60px;
  cursor: pointer;
  border: px solid;
  touch-action: manipulation;
  transition: all 0.3s ease 0s;
  color: #333;
  display: block;
  padding: 10px 0;
}
.navigation.post-navigation .nav-links .nav-next a:hover,
.navigation.post-navigation .nav-links .nav-previous a:hover {
  color: #222222;
  text-decoration: underline;
}

/*---------------------------
      Soical Share
----------------------------*/
.dostart-post-share {
  text-align: center;
